.container {
  box-shadow: 2px 4px 8px lightgray;

  transition: width 0.6s, background-color 0.6s, color 0.6s;
}
.container:hover {
  cursor: pointer;

  width: 70%;
  border-radius: 20px;
  color: white;
  background-color: #0d6efd;
}

.booklist_link {
  position: relative;
  text-decoration: none;
}
