.body {
  background-color: #fafafa;
}
.booklist_header {
  display: flex;
  padding: 10px;
}
.booklist_header > h1 {
  padding-left: 20px;
  display: flex;
  justify-content: center;
  text-align: center;
}
.back_link {
  text-decoration: none;
}

.book_details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 20px;
}
.book_details_main {
  background-color: white;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  width: 450px;
  box-shadow: 2px 3px 4px lightgrey;
  border: 1px solid lightgray;
}

.book_details_main_2 {
  margin-top: 10px;
}
.book_details_name {
  display: flex;
  flex-direction: column;
}
.book_details_item {
  display: flex;
  margin-left: 10px;
}
.favourite_main {
  display: flex;
  margin: 10px;
  margin-left: 50px;
  display: flex;
  flex-wrap: wrap;
}
