.fav_button {
  margin-right: 20px;
  background-color: #e58e8e;
  border: none;
  border-radius: 10px;
  width: 100px;
  padding: 5px;
  box-shadow: 1px 3px 5px #c44444;
  color: white;
  transition: background-color 0.8s, color 0.8s;
}
.fav_button:hover {
  background-color: #fc7272;
  color: lightgray;
}
